*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html{
    scroll-behavior: smooth;
}
.flex{
    display: flex;
}
body{
    height: 100vh;
    background: linear-gradient(#b296cc, #8ce492);
    font-family: 'Montserrat', sans-serif;
    color: #303E27;
    font-style: regular;
    font-size: clamp(13px, 10vw, 20px);
    line-height: 1.5;
    /* convention du web : séparer paragraphes de 1.3 */
}
img{
    /* garder ratio d'une image  */
    border-radius: 3px;
    object-fit: cover;
    display: block;
    max-width: 100%;
}
div{
    height: auto;
}
button{
    width: 30%;
    /* to center the button on the page width */
    display: block;
    margin: auto;
    border-radius: 30px;
    border: 1px solid #9627ff;
    background-color: #9627ff;
    margin-top: 3%;
    padding: 1%;
    /* padding: top right bottom left*/
    transition: background-color .5s;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    color: #FFFFFF;
    font-size: clamp(13px, 10vw, 20px);
    line-height: 30px;
    font-weight: 600;
    justify-content: center;
    align-self: center;
}
.addGarden{
    width: 30%;
}
button:hover {
    color: #FFFFFF !important;
    background-color: #303E27;
    border: 1px solid #303E27!important;
    cursor: pointer;
}
/* button a{
    color: #FFFFF4;
} */
button a:hover{
    color: #303E27;
}
p{
    font-size: clamp(13px, 10vw, 20px);
}
h1, h2, h3, h4, h5, h6{
    /* font-family: 'Lobster', cursive; */
    font-family: 'Montserrat Subrayada', sans-serif;
    font-weight: 700;
    color: #35462b;
}
h1{
    /* font-size: clamp(32px, 7vw, 42px); for Lobster font */
    font-size: clamp(25px, 7vw, 35px); /*for Montserrat Subrayada font */
    text-align: center;
    margin-top: 3%;
}
h4{
    font-size: clamp(20px, 7vw, 30px);
}
a{
    font-size: clamp(13px, 7vw, 20px);
    text-decoration: none;
    color: #FFFFF4;
    transition: color .5s;
}
.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.grid{
    display: grid;
    grid-gap: 0%;
 	grid-template-columns: repeat(5, 3.5rem);
 	grid-template-rows: repeat(5, 3.5rem);
    border: 1px solid #303E27;
    border-radius: 1%;
    margin: auto;
    margin-top: 3%;
}
.cell{
    background-color: #a3f8a8;
    /* border: 1px solid #9627ff; */
    border: 1px solid #303E27;
    border-radius: 1%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.text{
    text-align: center;
    display: none;
}
footer{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100vw;
    position: absolute;
    bottom: .5%;
}
.copyright{
    font-size: clamp(8px, 10vw, 12px);
}

/* 360×640 => Galaxy S5 */
/* @media screen and (max-width: 360px){

} */

/* 414×896 => iPhone 6/7/8+ */
@media screen and (max-width: 415px){
    button{
        width: 60% !important;
        margin-top: 10%;
    }
} 

/* 768x1024 => iPad  */
@media screen and (max-width: 768px){
    h1{
        margin-top: 20%;
    }
    button{
        width: 30%;
    }
    footer{
        bottom: 1%;
    }
}
